.loader_block {
  position: absolute;
  display: flex;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  align-items: center;
  justify-content: center;
  z-index: 10;
  font-size: 5rem;
  background-color: rgba(255, 255, 255, 0.5);
}

.loader_block rect {
  fill: #3097D1;
}

.status {
  padding: 5px 8px;
  background-color: #3097D1;
  color: #fff;
  margin: 0 25px;
  border-radius: 5px;
}

.status.done {
  background-color: #2ab27b;
}

.to-do-list h3 {
  border-bottom: 1px solid #b9b9b9;
  padding: 2rem 0;
}

.to-do-list .items .item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 1px solid #b9b9b9;
  padding: 7px 15px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 6px 4px 0px;
}

@media (max-width: 992px) {
  .to-do-list .items .item {
    flex-direction: column;
  }
}

.to-do-list .items .item .info {
  max-width: 500px;
  margin-right: 15px;
}

@media (max-width: 992px) {
  .to-do-list .items .item .info {
    margin-right: 0;
    margin-bottom: 1rem;
    max-width: 100%;
  }
}

.to-do-list .items .item .actions {
  display: flex;
  align-items: center;
  gap: 1rem;
}

@media (max-width: 500px) {
  .to-do-list .items .item .actions {
    flex-direction: column;
    width: 80%;
  }
}

@media (max-width: 500px) {
  .to-do-list .items .item .actions .btn {
    display: block;
    width: 80%;
  }
}

.to-do-list .items .item + .item {
  margin-top: 2rem;
}

.show_todo_item h4 {
  border-bottom: 1px solid #b9b9b9;
  padding-bottom: 1rem;
}

.container-custom {
  padding: 0 15px;
  margin: 0 auto;
  max-width: 1170px;
}
